.banner {
    color: white;
    object-fit: contain;
    height: 448px;
    margin-bottom: 30px;
    mask-image: linear-gradient(rgba(0,0,0,1) 400px, rgba(0,0,0,0));
}

.banner_contents{
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}

.banner__title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.banner__description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    height: 80px;
}

.banner__button{
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding: 0.5rem 2rem;
    margin: 0.5rem 2rem 0 0;
    background-color: rgba(51,51,51,0.5);
}

.banner__button:hover{
    color: black;
    background-color: #e6e6e6;
    transition: all 0.2s;
}