.nav{
    position: fixed;
    top: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    z-index: 100;
    padding: 20px;
    height: 25px;
    transition: ease-in 0.5s;
}

.nav__black{
    background-color: black;
    opacity: 0.9;
}

.nav__logo{
    position: fixed;
    left: 20px;
    object-fit: contain;
    width: 80px;
}

.nav__avatar{
    position: fixed;
    right: 20px;
    object-fit: contain;
    width: 30px;
}