.row{
    margin-left: 20px;
}

.row > h2 {
    color: white;
}

.row__posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}

.row__posters::-webkit-scrollbar {
    display: none;
}

.row__poster{
    cursor: pointer;
    object-fit: contain;
    max-height: 100px;
    margin-right: 10px;
    transition: transform 450ms;
}
.row__postersContainer:hover .row__poster{
    transform: scale(1.08);
    opacity: 0.1;
}
.row__postersContainer:hover .row__posterText{
    opacity: 0.9;
}

.row__postersContainer{
    position: relative;
    width: 100%;
}

.row__posterText{
    cursor: pointer;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-weight: 700;
    font-size: 1rem;
    text-align: center;
    color: wheat;
    opacity: 0;
    transition: .5s ease;
}

.row__posterLarge{
    max-height: 250px;
}